<module-view viewClass="change-avatar" viewId="changeAvatar"><module-view-form formClass="change-avatar__form"><module-view-title titleClass="change-avatar__form-title" :titleText="profile.changeAvatar.title"></module-view-title><img-file-input inputClass="change-avatar__form-file" inputId="changeAvatarFormFile" inputName="changeAvatarFormFile" labelClass="change-avatar__form-file-label" labelId="changeAvatarFormFileLabel" :inputLoad="profile.changeAvatar.inputLoad" :inputFileSucess="profile.changeAvatar.inputFileSucess" :inputFile="profile.changeAvatar.inputFile" @changeAction="preloadingImageAvatar($event)"></img-file-input><main-btn elType="button" elId="formRenameBtn" elClass="form-rename__form-btn" :elText="profile.changeAvatar.btnSubmit" @clickAction="changeAvatar()"></main-btn><module-view-btn-exit btnType="button" btnClass="change-avatar__form-btn-exit" :btnText="profile.changeAvatar.btnExit" @clickAction="toggleChangeAvatarFormModule(), loadProfileInputFile(), delPreloadImageAvatar($event)"></module-view-btn-exit></module-view-form></module-view>