<div class="todolist__form"><fast-form-list-create :inputPlace="todoForm.inputPlaceholder" :btnText="todoForm.btnText" btnClass="todolist__form-nav-btn" @clickCreate="getNewRecord()"></fast-form-list-create><hr v-if="personRecord.length != 0"><div class="todolist__form-body"><h3 class="todolist__form-body-title" v-if="personRecord.length == 0">{{ todoForm.title }}</h3><div v-else><draggable class="todolist__form-body-list list-group" id="list" tag="ul" :component-data="{ tag: 'transition-group', type: 'transition-group', name: 'flip-list' }" v-model="personList" v-bind="dragOptions" @end="dragRecordSave()" item-key="id" handle=".handle"><template #item="{ element, index }"><fast-form-list-item :idx="index" :text="element.text" @delItem="delRecord(index)"></fast-form-list-item></template></draggable></div></div><hr></div>