<module-view viewClass="form-rename" viewId="formRename"><module-view-form formClass="form-rename__form"><module-view-title titleClass="form-rename__form-title" :titleText="profile.rename.title"></module-view-title><main-input elType="text" :elPlaceholder="profile.rename.placeholder" elClass="form-rename__form-input" elId="formRenameInput" @inputAction="renameInputSave()" @keyup.enter.trim="changeUserName()"></main-input><main-btn elType="button" elId="formRenameBtn" elClass="form-rename__form-btn" @clickAction.trim="changeUserName()" :elText="profile.rename.btn"></main-btn><module-view-btn-exit btnType="button" btnClass="form-rename__form-btn-exit" :btnText="profile.rename.btnExit" @clickAction="toggleRenameFormModule()"></module-view-btn-exit></module-view-form></module-view>