<module-view viewClass="theme-rename-img" viewId="themeRenameTtl"><module-view-form formClass="theme-rename-img__form"><module-view-title titleClass="theme-rename-img__form-title" :titleText="theme.rename.img.title"></module-view-title><img-file-input inputClass="theme-rename-img__form-input" inputId="themeRenameTtlFormFile" inputName="themeRenameTtlFormName" labelClass="theme-rename-img__form-label" labelId="themeRenameTtlFormLabel" :inputLoad="theme.rename.img.inputLoad" :inputFileSucess="theme.rename.img.inputFileSucess" :inputFile="theme.rename.img.inputFile" @changeAction="preloadThemeRenameImgFormModule($event)"></img-file-input><main-btn elType="button" elId="themeRenameTtlBtn" elClass="theme-rename-img__form-btn" :elText="theme.rename.img.btnSub" @clickAction="uploadThemeRenameImgFormModule().then(() =&gt; { delThemeRenameImgFormModule($event), toggleThemeRenameImgFormModule(); })"></main-btn><module-view-btn-exit btnType="button" btnClass="theme-rename-img__form-btn-exit" :btnText="theme.rename.img.btnExit" @clickAction="toggleThemeRenameImgFormModule(), delInfoForRenameTheme()"></module-view-btn-exit></module-view-form></module-view>